export const convertNumberToVNDString = (num: number) => {
    if (num < 1000) {
        return `${num} triu`;
    } else {
        const x = num % 1000;
        return x !== 0 ? `${(num / 1000).toFixed(1)} t` : `${num / 1000} t`;
    }
};

